import { ActionType } from "@redux/action-types";
import { Action } from "@redux/actions";

interface SettingInfo {
  isAnimationOn: boolean;
}
const initialState: SettingInfo = { isAnimationOn: false };

export const SettingReducer = (
  state: SettingInfo = initialState,
  action: Action
): SettingInfo => {
  switch (action.type) {
    case ActionType.ANIMATION_ON:
      console.log("I'm here");
      return { isAnimationOn: true };
    case ActionType.ANIMATION_OFF:
      return { isAnimationOn: false };
    default:
      return state;
  }
};
