import { FunctionComponent, useState } from "react";
import { Link } from "react-router-dom";

export const DropDown: FunctionComponent = () => {
  const [isOpen, setIsOpen] = useState(false);
  const dropdownControl = (): void => {
    setIsOpen(!isOpen);
  };
  const closeModal = (): void => {
    setIsOpen(false);
  };
  return (
    <div className="relative bg-gray-700 mr-4">
      <button
        onClick={dropdownControl}
        className="relative z-40  block hover:text-white"
      >
        menu
      </button>
      {isOpen ? (
        <>
          <button
            tabIndex={-1}
            className="fixed inset-0 h-full w-full bg-gray-300 opacity-40 cursor-default"
            onClick={closeModal}
          ></button>
          <div className="absolute shadow-lg right-0  mt-2 w-24 bg-white rounded-lg py-2">
            <Link
              to="/"
              className="block px-4 py-2 text-gray-700 hover:bg-indigo-500 hover:text-white text-center text-sm"
              onClick={closeModal}
            >
              HOME
            </Link>
            <Link
              to="/result"
              className="block px-4 py-2 text-gray-700 hover:bg-indigo-500 hover:text-white text-center text-sm"
              onClick={closeModal}
            >
              RESULT
            </Link>
            <Link
              to="/settings"
              className="block px-4 py-2 text-gray-700 hover:bg-indigo-500 hover:text-white text-sm "
              onClick={closeModal}
            >
              SETTINGS
            </Link>
          </div>
        </>
      ) : null}
    </div>
  );
};
